<script>
import { mapActions, mapGetters } from 'vuex'
import Datepicker from 'vuejs-datepicker'
import VueNumeric from 'vue-numeric'
import searchSm from '../components/SearchSm.vue'
export default {
  data: function () {
    return {
      newEmployee: false,
      errors: [],
      searchDept: {
        attr1: { name: 'id', label: 'Department Code' },
        attr2: { name: 'departmentName', label: 'Department Name' },
        attr3: { name: 'divisionName', label: 'Division Name' }
      },
      departmentList: [],
      department: {},
      empDepartmentName: '',
      totalRows: 0
    }
  },
  components: {
    Datepicker,
    VueNumeric,
    searchSm
  },
  methods: {
    ...mapActions([
      'resetPayrollVar',
      'setEmployeeFormDisplay',
      'saveEmployee',
      'updateEmployee',
      'fetchEmployee',
      'defaultState',
      'updateEmployeeRecord',
      'updateGosiAmount'
    ]),
    validateDepartmentCode () {
      this.fetchDepartmentCode(this.employee.deptCode)
    },
    async updateGosi () {
      console.log('contractType' + this.employee.contractType)
      if (this.employee.contractType === 0) {
        this.updateGosiAmount(this.employee.basicSalary * 0.08)
      } else {
        this.updateGosiAmount(0)
      }
    },
    // async getEmployee () {
    //   const x = this.employee.id
    //   const url = process.env.VUE_APP_API_URL + `/employee/${this.employee.id}`
    //   fetch(url)
    //     .then((res) => res.json())
    //     .then((data) => {
    //       if (data.id === 0) {
    //         console.log('no data')
    //         this.defaultState()
    //         console.log(data)
    //         this.employee.id = x
    //         this.newEmployee = true
    //       } else {
    //         this.updateEmployeeRecord(data)
    //         this.newEmployee = false
    //         console.log(data)
    //       }
    //     })
    //     .catch((err) => console.error(err))
    //   if (this.message.length !== 0) {
    //     this.$toasted.show(this.message, {
    //       theme: 'bubble',
    //       position: 'top-right',
    //       duration: 5000
    //     })
    //   }
    // },
    fetchDepartmentList () {
      const url = process.env.VUE_APP_API_URL + '/departmentView'
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          this.departmentList = data
          this.totalRows = this.departmentList.length
        })
        .catch((err) => console.error(err))
    },
    async getEmployee () {
      const x = this.employee.id
      const url = process.env.VUE_APP_API_URL + `/employee/${this.employee.id}`
      try {
        const res = await fetch(url)
        const data = await res.json()
        if (data.id === 0) {
          console.log('no data')
          this.defaultState()
          console.log(data)
          this.employee.id = x
          this.newEmployee = true
        } else {
          this.updateEmployeeRecord(data)
          this.newEmployee = false
          this.fetchDepartmentCode(this.employee.deptCode)
          console.log(data)
        }
      } catch (err) {
        console.error(err)
      }
      if (this.message.length !== 0) {
        this.$toasted.show(this.message, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
    },
    fetchDepartmentCode (x) {
      // alert('fetchDepartmentCode:' + x)
      this.errors = []
      const url = process.env.VUE_APP_API_URL + `/department/${x}`
      fetch(url)
        .then((res) => res.json())
        .then((data) => {
          this.department = data
          console.log(this.department)
          this.employee.deptCode = this.department.id
          this.empDepartmentName = this.department.departmentName
        })
        .catch((err) => {
          // console.error(err)
          alert(err)
          // this.errors.push('Invalid Department code')
          // this.showErrorMessage('Invalid Department code')
        })
    },
    showErrorMessage (msg) {
      this.$toasted.show(msg, {
        theme: 'bubble',
        position: 'top-right',
        duration: 5000
      })
    },
    checkForm: function (e) {
      console.log('checking form data')
      // alert(this.employee.empName.length)
      this.errors = []
      if (this.employee.empCode === 0) {
        this.errors.push('Code Required.')
      }
      if (!this.employee.empName) {
        this.errors.push('Name Required.')
      }
      if (this.employee.empName.length > 40) {
        this.errors.push('Name should not be more than 40 chr')
      }
      if (this.employee.empName.length < 3) {
        this.errors.push('Name should be at least 3 chr')
      }
      if (!this.employee.deptCode) {
        this.errors.push('Department Required.')
      }
      if (!this.employee.cprNo) {
        this.errors.push('Employee CPR Required.')
      }
      if (!this.employee.basicSalary) {
        this.errors.push('Basic Salary Required.')
      }
      if (!this.errors.length) {
        return true
      } else {
        this.errors.unshift(
          'Fields highlighted in yellow are required, please enter following ..'
        )
      }
    },
    houseKeeping () {
      this.errors = []
      this.empDepartmentName = ''
    },
    async save () {
      this.checkForm()
      if (!this.errors.length) {
        if (this.newEmployee) {
          await this.saveEmployee(this.employee)
        } else {
          await this.updateEmployee(this.employee)
        }
        this.$toasted.show(this.message, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      } else {
        this.$toasted.show(this.errors, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
      this.houseKeeping()
    }
  },
  computed: {
    ...mapGetters(['employee', 'message'])

  }
}
</script>

